.modal {
  position: fixed;
  top: 5%;
  left: 20%;
  width: 65%;
  min-height: 85%;
  border-radius: 10px;
  background-color: #141414;
  z-index: 10000;
  /* overflow: scroll; */
  will-change: scroll-position;
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 999;
}
.close {
  position: absolute;
  top: 2%;
  right: 2%;
  z-index: 12;
  font-size: 50px;
  color: #fff;
  text-shadow: 1px 1px 5px #141414;
  border-radius: 50%;
  cursor: pointer;
}
.top-modal {
  height: 400px;
  background-size: 1200px;
  position: relative;
  border-radius: 10px;
}
.details {
  position: absolute;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, #141414 40%, transparent);
  width: 100%;
  padding-left: 30px;
  padding-bottom: 30px;
}
.name {
  font-size: 60px;
  text-shadow: 5px 5px 10px #141414;
  /* margin-top: 10px; */
}
.buttons {
  display: flex;
}
.buttons a {
  display: block;
  font-size: 20px;
  font-weight: bold;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  padding: 10px 15px;
  /* border-radius: 5px; */
}
.buttons a:hover {
  opacity: 0.7;
}
.btn-play i {
  margin-right: 15px;
}
.btn-play {
  color: #000;
  background: #fff;
  border-radius: 5px;
}
.circle {
  color: #fff;
  border: 1px solid #fff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.bottom {
  display: flex;
}
.info {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-bottom: 30px;
}
.info-top {
  display: flex;
  font-size: 20px;
  margin-bottom: 25px;
  font-weight: bold;
}
.info-top div {
  margin-right: 10px;
}
.relevance {
  color: #46de69;
}
.description {
  font-size: 18px;
  max-width: 60%;
}
.genres {
  margin-right: 10%;
  font-size: 20px;
}

@media (max-width: 1000px) {
  .modal {
    left: 5%;
    width: 90%;
  }
}
@media (max-width: 850px) {
  .modal {
    width: 100%;
    left: 0%;
  }
  .name {
    font-size: 25px;
  }
  .info {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .top-modal {
    height: 200px;
  }
  .description {
    max-width: 100%;
  }
  .bottom {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 650px) {
  .name {
    font-size: 20px;
  }

  .description {
    font-size: 12px;
  }
  .genres {
    font-size: 10px;
  }
  .top-modal {
    height: 150px;
  }
}
@media (max-width: 400px) {
  .top-modal {
    height: 400px;
  }
  .description {
    font-size: 18px;
  }
}
