header {
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s;
  width: 100%;
  padding: 10px 20px;
}
.black {
  background-color: #141414;
  transition: all 0.2s;
}
.header--logo {
  width: 200px;
}
.header--logo img {
  width: 100%;
}
.header--user-icon {
  width: 75px;
}
.header--user-icon img {
  width: 100%;
  border-radius: 5px;
}
@media (max-width: 950px) {
  .header--logo {
    width: 100px;
  }
  .header--user-icon {
    width: 50px;
  }
}
