.movieRow {
  margin: 30px 0;
  position: relative;
}

.movieRow h2 {
  margin: 0;
  margin-left: 30px;
  font-size: 45px;
}

.movieRow--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}
.movieRow--list {
  width: 100%;
  display: flex;
  transition: all 0.2s;
}
.movieRow--item {
  display: inline-block;
  cursor: pointer;
}
.movieRow--item img {
  transform: scale(0.9);
  transition: all 0.2s;
}
.movieRow--item img:hover {
  transform: scale(1);
}
.left-arrow,
.right-arrow {
  height: 83%;
  margin-top: 1%;
  background-color: rgba(0, 0, 0, 0.7);
  width: 40px;
  font-size: 50px;
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: all 0.15s;
}
.left-arrow {
  left: 0;
}
.right-arrow {
  right: 0;
}
.movieRow:hover .left-arrow,
.movieRow:hover .right-arrow {
  opacity: 1;
}

@media (max-width: 1400px) {
  .movieRow h2 {
    font-size: 30px;
  }
  .left-arrow,
  .right-arrow {
    opacity: 1;
    height: 87%;
  }
}
