.featured {
  height: 90vh;
  background-size: cover;
  background-position: center top;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 5%, transparent);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 17%, transparent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
}
.featured--name {
  font-size: 55px;
  font-weight: bolder;
}
.featured--info {
  font-size: 20px;
  font-weight: bold;
  display: flex;
  margin-top: 15px;
}

.featured--info div {
  margin: 0 10px;
}
.featured--relevance {
  color: #46de69;
}
.featured--description {
  margin-top: 15px;
  font-size: 22px;
  color: #999;
  max-width: 40%;
}
.featured--buttons {
  margin-top: 15px;
  display: flex;
}
.featured--buttons a {
  display: block;
  font-size: 20px;
  margin: 0 12px;
  padding: 15px 25px;
  border-radius: 5px;
  font-weight: bold;
  transition: all 0.2s;
}
.featured--buttons i {
  margin-right: 10px;
}
.featured--btn-play {
  color: #000;
  background: #fff;
}
.featured--btn-list {
  color: #fff;
  background: #333;
}
.featured--buttons a:hover {
  opacity: 0.7;
}
.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width: 1024px) {
  .featured--description {
    max-width: 70%;
    font-size: 15px;
  }
}
@media (max-width: 950px) {
  .featured {
    background-position: center right;
  }
  .featured--horizontal {
    padding-top: 10%;
  }
  .featured--name {
    font-size: 30px;
  }
  .featured--description {
    max-width: 100%;
    font-size: 14px;
  }
  .featured--buttons a {
    font-size: 16px;
  }
  .featured--info {
    font-size: 14px;
  }
  .featured--genres {
    font-size: 12px;
  }
}

@media (max-width: 640px) {
  .featured--name {
    font-size: 20px;
  }
  .featured--buttons a {
    font-size: 12px;
    padding: 8px 12px;
  }
}
