.featured {
  height: 90vh;
  background-size: cover;
  background-position: center top;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 5%, transparent);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 17%, transparent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
}
.featured--name {
  font-size: 55px;
  font-weight: bolder;
}
.featured--info {
  font-size: 20px;
  font-weight: bold;
  display: flex;
  margin-top: 15px;
}

.featured--info div {
  margin: 0 10px;
}
.featured--relevance {
  color: #46de69;
}
.featured--description {
  margin-top: 15px;
  font-size: 22px;
  color: #999;
  max-width: 40%;
}
.featured--buttons {
  margin-top: 15px;
  display: flex;
}
.featured--buttons a {
  display: block;
  font-size: 20px;
  margin: 0 12px;
  padding: 15px 25px;
  border-radius: 5px;
  font-weight: bold;
  transition: all 0.2s;
}
.featured--buttons i {
  margin-right: 10px;
}
.featured--btn-play {
  color: #000;
  background: #fff;
}
.featured--btn-list {
  color: #fff;
  background: #333;
}
.featured--buttons a:hover {
  opacity: 0.7;
}
.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width: 1024px) {
  .featured--description {
    max-width: 70%;
    font-size: 15px;
  }
}
@media (max-width: 950px) {
  .featured {
    background-position: center right;
  }
  .featured--horizontal {
    padding-top: 10%;
  }
  .featured--name {
    font-size: 30px;
  }
  .featured--description {
    max-width: 100%;
    font-size: 14px;
  }
  .featured--buttons a {
    font-size: 16px;
  }
  .featured--info {
    font-size: 14px;
  }
  .featured--genres {
    font-size: 12px;
  }
}

@media (max-width: 640px) {
  .featured--name {
    font-size: 20px;
  }
  .featured--buttons a {
    font-size: 12px;
    padding: 8px 12px;
  }
}

header {
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s;
  width: 100%;
  padding: 10px 20px;
}
.black {
  background-color: #141414;
  transition: all 0.2s;
}
.header--logo {
  width: 200px;
}
.header--logo img {
  width: 100%;
}
.header--user-icon {
  width: 75px;
}
.header--user-icon img {
  width: 100%;
  border-radius: 5px;
}
@media (max-width: 950px) {
  .header--logo {
    width: 100px;
  }
  .header--user-icon {
    width: 50px;
  }
}

.modal {
  position: fixed;
  top: 5%;
  left: 20%;
  width: 65%;
  min-height: 85%;
  border-radius: 10px;
  background-color: #141414;
  z-index: 10000;
  /* overflow: scroll; */
  will-change: scroll-position;
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 999;
}
.close {
  position: absolute;
  top: 2%;
  right: 2%;
  z-index: 12;
  font-size: 50px;
  color: #fff;
  text-shadow: 1px 1px 5px #141414;
  border-radius: 50%;
  cursor: pointer;
}
.top-modal {
  height: 400px;
  background-size: 1200px;
  position: relative;
  border-radius: 10px;
}
.details {
  position: absolute;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, #141414 40%, transparent);
  width: 100%;
  padding-left: 30px;
  padding-bottom: 30px;
}
.name {
  font-size: 60px;
  text-shadow: 5px 5px 10px #141414;
  /* margin-top: 10px; */
}
.buttons {
  display: flex;
}
.buttons a {
  display: block;
  font-size: 20px;
  font-weight: bold;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  padding: 10px 15px;
  /* border-radius: 5px; */
}
.buttons a:hover {
  opacity: 0.7;
}
.btn-play i {
  margin-right: 15px;
}
.btn-play {
  color: #000;
  background: #fff;
  border-radius: 5px;
}
.circle {
  color: #fff;
  border: 1px solid #fff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.bottom {
  display: flex;
}
.info {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-bottom: 30px;
}
.info-top {
  display: flex;
  font-size: 20px;
  margin-bottom: 25px;
  font-weight: bold;
}
.info-top div {
  margin-right: 10px;
}
.relevance {
  color: #46de69;
}
.description {
  font-size: 18px;
  max-width: 60%;
}
.genres {
  margin-right: 10%;
  font-size: 20px;
}

@media (max-width: 1000px) {
  .modal {
    left: 5%;
    width: 90%;
  }
}
@media (max-width: 850px) {
  .modal {
    width: 100%;
    left: 0%;
  }
  .name {
    font-size: 25px;
  }
  .info {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .top-modal {
    height: 200px;
  }
  .description {
    max-width: 100%;
  }
  .bottom {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 650px) {
  .name {
    font-size: 20px;
  }

  .description {
    font-size: 12px;
  }
  .genres {
    font-size: 10px;
  }
  .top-modal {
    height: 150px;
  }
}
@media (max-width: 400px) {
  .top-modal {
    height: 400px;
  }
  .description {
    font-size: 18px;
  }
}

